import { AppComponent } from './app.component';
import { CommandComponent } from '../command/command.component';

@NgModule({
  declarations: [
    AppComponent,
    CommandComponent,
  ],
  imports: [
    BrowserModule,
    FormsModule,
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



